/**
 * External dependencies
 */
import { pickBy } from 'lodash';

/**
 * GeChiUI dependencies
 */
import { __, sprintf } from '@gechiui/i18n';
import { MenuGroup, MenuItem } from '@gechiui/components';
import { store as blockEditorStore } from '@gechiui/block-editor';
import { useDispatch, useSelect } from '@gechiui/data';
import { store as editorStore } from '@gechiui/editor';
import { store as coreStore } from '@gechiui/core-data';

/**
 * Internal dependencies
 */
import { store as editPostStore } from '../../../store';

export default function DeleteTemplate() {
	const { clearSelectedBlock } = useDispatch( blockEditorStore );
	const { setIsEditingTemplate } = useDispatch( editPostStore );
	const { getEditorSettings } = useSelect( editorStore );
	const { updateEditorSettings, editPost } = useDispatch( editorStore );
	const { deleteEntityRecord } = useDispatch( coreStore );
	const { template } = useSelect( ( select ) => {
		const { isEditingTemplate, getEditedPostTemplate } = select(
			editPostStore
		);
		const _isEditing = isEditingTemplate();
		return {
			template: _isEditing ? getEditedPostTemplate() : null,
		};
	}, [] );

	if ( ! template || ! template.gc_id ) {
		return null;
	}
	let templateTitle = template.slug;
	if ( template?.title ) {
		templateTitle = template.title;
	}

	return (
		<MenuGroup className="edit-post-template-top-area__second-menu-group">
			<MenuItem
				className="edit-post-template-top-area__delete-template-button"
				isDestructive
				variant="secondary"
				aria-label={ __( 'Delete template' ) }
				onClick={ () => {
					if (
						// eslint-disable-next-line no-alert
						window.confirm(
							sprintf(
								/* translators: %s: template name */
								__(
									'Are you sure you want to delete the %s template? It may be used by other pages or posts.'
								),
								templateTitle
							)
						)
					) {
						clearSelectedBlock();
						setIsEditingTemplate( false );

						editPost( {
							template: '',
						} );
						const settings = getEditorSettings();
						const newAvailableTemplates = pickBy(
							settings.availableTemplates,
							( _title, id ) => {
								return id !== template.slug;
							}
						);
						updateEditorSettings( {
							...settings,
							availableTemplates: newAvailableTemplates,
						} );
						deleteEntityRecord(
							'postType',
							'gc_template',
							template.id
						);
					}
				} }
			>
				{ __( 'Delete template' ) }
			</MenuItem>
		</MenuGroup>
	);
}
