/**
 * GeChiUI dependencies
 */
import { MenuItem } from '@gechiui/components';
import { useSelect, useDispatch } from '@gechiui/data';
import { __ } from '@gechiui/i18n';
import { useCopyToClipboard } from '@gechiui/compose';
import { store as noticesStore } from '@gechiui/notices';
import { store as editorStore } from '@gechiui/editor';

export default function CopyContentMenuItem() {
	const { createNotice } = useDispatch( noticesStore );
	const getText = useSelect(
		( select ) => () =>
			select( editorStore ).getEditedPostAttribute( 'content' ),
		[]
	);

	function onSuccess() {
		createNotice( 'info', __( 'All content copied.' ), {
			isDismissible: true,
			type: 'snackbar',
		} );
	}

	const ref = useCopyToClipboard( getText, onSuccess );

	return <MenuItem ref={ ref }>{ __( 'Copy all content' ) }</MenuItem>;
}
