/**
 * GeChiUI dependencies
 */
import { MenuItem } from '@gechiui/components';
import { withDispatch } from '@gechiui/data';
import { __ } from '@gechiui/i18n';
import { displayShortcut } from '@gechiui/keycodes';

/**
 * Internal dependencies
 */
import { store as editPostStore } from '../../store';

export function KeyboardShortcutsHelpMenuItem( { openModal } ) {
	return (
		<MenuItem
			onClick={ () => {
				openModal( 'edit-post/keyboard-shortcut-help' );
			} }
			shortcut={ displayShortcut.access( 'h' ) }
		>
			{ __( 'Keyboard shortcuts' ) }
		</MenuItem>
	);
}

export default withDispatch( ( dispatch ) => {
	const { openModal } = dispatch( editPostStore );

	return {
		openModal,
	};
} )( KeyboardShortcutsHelpMenuItem );
